.card {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  width: 200px;
  padding: 16px;
  border: 1px solid $border-color;
  border-radius: 5px;
  transition: 300ms;

  &__image {
    align-self: center;
    width: 160px;
    height: 134px;
    margin: 17px 3px 20px;
  }

  &__name {
    width: $standard-width;
    font-weight: $medium-font-weight;
    color: $black-color;
    font-size: 12px;
    margin: 20px 0 4px;
    line-height: 18px;
    align-self: center;
  }

  &:hover {
    transform: scale(1.2);
    .card__name {
      color: $card-name-color-hovered;
    }
  }

  &__code {
    font-weight: $regular-font-weight;
    width: $standard-width;
    color: $gray-color;
    margin: 0 0 16px;
    font-size: 10px;
    line-height: 14px;
    align-self: center;
    text-align: left;
  }

  &__ratings {
    width: $standard-width;
    display: flex;
    align-self: center;
    align-items: center;
    justify-content: space-between;
    height: 16px;
    margin-bottom: 24px;
  }

  &__reviews {
    font-weight: $regular-font-weight;
    font-size: 10px;
    margin: 0;
  }

  &__price {
    width: $standard-width;
    display: flex;
    align-items: center;
    align-self: center;
    justify-content: space-between;
    height: 18px;
  }

  &__value {
    font-weight: $bold-font-weight;
    font-size: 16px;
    margin: 0;
    line-height: 18px;
  }

  &__label {
    color: $gray-color;
    font-weight: $regular-font-weight;
    margin: 0;
    font-size: 12px;
    line-height: 18px;
  }

  &__button {
    color: $white-color;
    width: $standard-width;
    background-color: $blue-color;
    font-weight: $bold-font-weight;

    display: flex;
    justify-content: center;
    text-align: center;
    box-sizing: border-box;
    border-radius: 5px;
    padding: 12px;
    margin: 0;
    font-size: 14px;
    line-height: 16px;
    margin-top: 16px;
    text-transform: uppercase;
    text-decoration: none;
    align-self: center;
  }

  &__button:hover {
    color: $blue-color;
    border: 1px solid $blue-color;
    background: #fff;
    box-sizing: border-box;
    height: 40px;
  }
}
